html {
  --purple-: #9674d4;
  --midnight-blue-: #101356;
  --off-white-: #fbfdf6;
  --bright-blue-: #81ffff;
  --grey-: #e8e8e8;
}

body {
  box-sizing: border-box;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  background-color: var(--midnight-blue-);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 2vh;
}

#top-intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: var(--off-white-);
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
}

#img {
  height: 70%;
}

.mid {
  background-color: var(--off-white-);
  border-radius: 2vh;
  padding: 2vh 2vh 0 2vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: right;
  text-align: center;
  background: var(--off-white-);
  color: var(--midnight-blue-);
  max-height: 60vh;
  height: 60vh;
  width: 80vh;

}

.map {
  position: relative;
  height: 50%;
  width: 100%;
  border-radius: 2vh;
  display: flex;
  justify-content: center;
}

button {
  height: 5vh;
  width: 18vh;
  border-radius: 4vh;
  border: none;
  transition: 0.3s;
  cursor: pointer;
  font-weight:bold;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1vh;
}

button:hover {
  box-shadow: 2px 2px 8px var(--grey-);
}


